<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">May 2019 Event Ticket Offer</h1>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-7">
          <p class="is-size-4">
            Congratulations
            <span class="has-text-primary">{{ recipientName }}</span>
          </p>
          <p>
            You have recieved an offer to buy a ticket for the TEDxAuckland
            event on May the 1st at Whoa! Studios.
          </p>
          <p>
            During the first part of the evening you will be inspired and
            entertained by some of Aucklands top thought leaders. Following this
            will be social time with the speakers and a five course meal
            loveingly crafted with West Auckland produce by acclaimed and
            multi-award winning Kiwi chef, Ben Bayly
          </p>
          <p>
            This event is limited to 160 attendees. You have
            <strong class="has-text-primary">48 hours</strong>
            to purchase your ticket. If you don't purchase within this timeframe
            the opportunity will be transferred to another applicant.
          </p>
          <p>
            We look forward to seeing you there and hope you enjoy the show.
          </p>
          <p>
            <strong class="has-text-primary">
              TEDxAuckland
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="acceptTicket">
            <h2 class="title is-3">Questions for Ticket</h2>
            <TicketQuestions
              class="section-spacing"
              :questions="ticketQuestions"
              :$v="$v"
            />

            <div class="buttons has-addons is-right">
              <button
                @click.prevent="isRejectModalActive = true"
                class="button is-warning is-medium"
              >
                Give Up Ticket
              </button>

              <b-modal :active.sync="isRejectModalActive">
                <div class="modal-card center-modal">
                  <header class="modal-card-head">
                    <p class="modal-card-title">Give Up Ticket Offer</p>
                    <button
                      class="delete"
                      @click.prevent="isRejectModalActive = false"
                      aria-label="close"
                    ></button>
                  </header>
                  <section class="modal-card-body">
                    <p>Are you sure you want to give up your ticket offer?</p>
                    <p>
                      By clicking the button below you are giving up your
                      opportunity to buy a ticket and releasing it to another
                      applicant.
                    </p>
                  </section>
                  <footer class="modal-card-foot">
                    <button
                      @click.prevent="
                        rejectTicket();
                        isRejectModalActive = false;
                      "
                      class="button is-warning"
                    >
                      Give up Ticket
                    </button>
                    <button
                      @click.prevent="isRejectModalActive = false"
                      class="button"
                    >
                      Cancel
                    </button>
                  </footer>
                </div>
              </b-modal>

              <input
                class="button is-primary is-medium"
                type="submit"
                value="Purchase through iTicket"
              />
            </div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import TicketQuestions from "@/components/TicketQuestions.vue";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import ticketQuestionsValidations from "@/validations/TicketQuestionsValidations";

export default {
  name: "TicketOfferFromApplication",
  components: {
    TicketQuestions
  },
  props: {
    recipient: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      isRejectModalActive: false,
      person: this.recipient,
      ticketQuestions: {
        organisation: this.recipient.data.organisation,
        suburb: this.recipient.data.suburb,
        area: this.recipient.data.area,
        founded: this.recipient.data.founded,
        conversationStarters: {
          one: this.recipient.data.conversation_starters
            ? this.recipient.data.conversation_starters.one
            : "",
          two: this.recipient.data.conversation_starters
            ? this.recipient.data.conversation_starters.two
            : "",
          three: this.recipient.data.conversation_starters
            ? this.recipient.data.conversation_starters.three
            : ""
        }
      }
    };
  },
  computed: {
    recipientName() {
      return `${this.recipient.first_name} ${this.recipient.last_name}`;
    }
  },
  validations: {
    ticketQuestions: ticketQuestionsValidations
  },
  methods: {
    acceptTicket() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        // Send to iTicket Website and wait for success webhook
        TicketService.acceptTicket({
          id: this.ticketOffer.id,
          ticketQuestions: this.ticketQuestions
        })
          .then(() => {
            NProgress.done();
            console.log("Send to the iTicket Website");
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    },

    rejectTicket() {
      NProgress.start();
      TicketService.rejectTicket({
        id: this.ticketOffer.id
      })
        .then(() => {
          NProgress.done();
          this.$router.push({
            name: "ticket-offer-surrendered",
            params: { id: this.ticketOffer.id }
          });
        })
        .catch(e => {
          NProgress.done();
          console.log("ERROR", e);
        });
    }
  }
};
</script>
